body {
	padding: 0;
	margin: 0;
}

* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
::-moz-selection {
	background: #007aff30;
}
::selection {
	background: #007aff30;
}
::-moz-selection {
	background: #007aff30;
}

/* iPad */
@media screen and (min-width: 767px) and (max-width: 1260px) {
	.search-input {
		width: 97% !important;
	}
	.editor-navigation {
		grid-template-columns: 0fr 1fr 1fr 1fr !important;
	}
	.file-pane {
		position: fixed;
		left: -2000px;
		bottom: 0;
		top: 50px;
		box-shadow: 2px 6px 15px rgba(0, 0, 0, 0.25);
	}
}
/* iPad Portrait */
@media only screen and (min-device-width: 767px) and (max-device-width: 1260px) and (orientation: portrait) {
	.code-pane {
		border: none !important;
	}
}
/* iPad Landscape */
@media only screen and (min-device-width: 767px) and (max-device-width: 1260px) and (orientation: landscape) {
}
/* Phone Responsiveness */
@media screen and (min-width: 120px) and (max-width: 767px) {
	.editor-title {
		display: none !important;
	}
	.file-list li.open-folder > ul > li {
		width: 96% !important;
	}
	.result-pane {
		width: auto !important;
		right: 0 !important;
		left: 0;
		bottom: 0;
		height: 93% !important;
		padding-bottom: 0 !important;
		border-radius: 0;
	}
	#download-result h1 {
		opacity: 0 !important;
	}
	.modal .large-btn {
		margin: 10px 20px !important;
	}
	.search-container input {
		width: 70% !important;
		min-width: auto !important;
		margin-left: 8px !important;
	}
	.editor-navigation > div:last-child {
		width: 200px;
	}
	.editor-navigation {
		grid-template-columns: 0fr 0fr 1fr 1fr !important;
	}
	.file-pane {
		position: fixed;
		left: -2000px;
		bottom: 0;
		top: 50px;
		right: 0;
		width: 100% !important;
		overflow-x: hidden !important;
	}
	.code-pane,
	.result-pane {
		border: none !important;
	}
	.code-pane {
		border: none !important;
	}
	.result-pane {
		height: 44vh;
		border: none !important;
	}
	.error-messages {
		bottom: 0;
		left: 0;
		padding: 0 !important;
		position: relative !important;
		margin: 0 !important;
	}
	.e-message {
		border-radius: 0 !important;
		display: block;
		border: none !important;
		padding: 15px 0 !important;
		padding-left: 40px !important;
		margin: 0 !important;
	}
	.search-action-results {
		position: fixed !important;
		top: 51px !important;
		left: 0 !important;
		right: 0 !important;
		bottom: 0;
		box-shadow: none !important;
		border-radius: 0 !important;
		width: auto !important;
		max-height: 100% !important;
	}
}
/* General Responsiveness */
@media screen and (min-width: 120px) and (max-width: 1260px) {
	.editor-menu {
		display: none !important;
	}
	.editor-container {
		display: block !important;
		grid-template-columns: auto !important;
		position: static !important;
		margin-top: 50px;
	}
	.code-pane {
		height: 93vh;
	}
	.result-pane {
		position: fixed;
		z-index: 200;
		height: 500px;
		right: 80px;
		top: 45px;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
		box-shadow: -1px 5px 16px rgba(0, 0, 0, 0.27) !important;
		border: 2px solid #ccc;
	}
	.download-grid > div:first-child:after {
		display: none;
	}
	.result-pane.mobiledisabled {
		display: none;
	}
	.close-search-btn {
		display: none !important;
	}
	.close-mobile-search-btn {
		display: block !important;
	}
	#run-preview {
		display: inline-block !important;
	}
	.loading-result-progress {
		right: 0 !important;
		left: 0;
		width: auto !important;
	}
	.file-pane.open-menu {
		left: 0;
	}
	.result-details {
		display: none !important;
	}
	.editor-navigation div:nth-child(2) > .editor-btn {
		display: none !important;
	}
	.mobile-filemenu {
		display: block !important;
	}
	.modals-container {
		-webkit-justify-content: unset !important;
		        justify-content: unset !important;
		-webkit-align-items: unset !important;
		        align-items: unset !important;
	}
	.modal {
		width: 100% !important;
		padding: 0 !important;
		border-radius: 0 !important;
		position: absolute;
		bottom: 0;
		top: 0;
		left: 0;
		right: 0;
		overflow: auto;
	}
	.download-grid {
		display: block !important;
	}
	.download-grid > div:first-child {
		border-right: none !important; /* border-bottom: 1px solid #eee; */
	}
	.download-grid > div:last-child {
		display: none;
	}
	.code-pane #ace_editor {
		height: 100vh !important;
	}
}
