	width: 100%;
}
.load {
	border-bottom: 3px solid rgba(150, 150, 150, 0.2);
	border-left: 3px solid rgba(150, 150, 150, 0.2);
	border-right: 3px solid rgba(0, 122, 255, 1);
	border-top: 3px solid rgba(0, 122, 255, 1);
	border-radius: 100%;
	height: 30px;
	width: 30px;
	-webkit-animation: rot 0.5s infinite linear;
	animation: rot 0.5s infinite linear;
}
.large {
	height: 50px;
	width: 50px;
}
.small {
	height: 20px;
	width: 20px;
	border-width: 3px !important;
}
@-webkit-keyframes rot {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(359deg);
		        transform: rotate(359deg);
	}
}
@keyframes rot {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(359deg);
		        transform: rotate(359deg);
	}
}

