	position: -webkit-sticky;
	position: sticky;
	top: 0;
	font-size: 13px;
	font-weight: 500;
	padding: 3px 5px;
	z-index: 1000;
}
/* Color & Glyph radios style */
.radio {
	display: inline-block;
}
.radio label {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding: 15px 20px;
	margin: 10px 5px;
	cursor: pointer;
}
input[type="radio"] {
	display: none;
}
.radio label:hover,
.radio label:hover:before {
	cursor: pointer;
}
.radio label:before {
	content: "";
	display: inline-block;
	width: 30px;
	height: 30px;
	margin-top: -15px;
	position: absolute;
	left: 0;
	border: 2px solid #fff;
	box-shadow: 0 0 0 2px #fff;
	transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
